import React, { Component } from 'react';
import {BrowserRouter as Router, NavLink} from 'react-router-dom';
import Route from 'react-router-dom/Route';
import About from './components/About';
import Experience from './components/Experience';
import Education from './components/Education';
import Skills from './components/Skills';
import Awards from './components/Awards';


class Header extends Component {
  render() {
    return (
      <div>
        <Router>
          <div>
          <nav className="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="sideNav">
            <span className="d-none d-lg-block">
              <img className="img-fluid img-profile rounded-circle mx-auto mb-2" src="img/profile.jpg" alt="" />
            </span>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav">
              <li className="nav-item">
                <NavLink className="nav-link js-scroll-trigger" to="/" exact activeStyle={{color:'white'}} >About</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link js-scroll-trigger" to="/experience" exact activeStyle={{color:'white'}} >Experience</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link js-scroll-trigger" to="/education" exact activeStyle={{color:'white'}} >Education</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link js-scroll-trigger" to="/skills" exact activeStyle={{color:'white'}} >Skills</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link js-scroll-trigger" to="/awards" exact activeStyle={{color:'white'}} >Awards</NavLink>
              </li>
            </ul>
          </div>
        </nav>
        <div class="container-fluid p-0">
        <Route  path="/" exact strict  component = {About}/>
        <Route  path="/experience" exact strict  component = {Experience}/>
        <Route  path="/education" exact strict  component = {Education}/>
        <Route  path="/skills" exact strict  component = {Skills}/>
        <Route  path="/awards" exact strict  component = {Awards}/>
        </div>
        </div>
        </Router>
      </div>
    );
  }
}

export default Header;